@import '~node_modules/modern-normalize/modern-normalize.css';

body {
  font-family: $primaryFontFamily;
  color: $primaryDarkTextColor;
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: $primaryDarkTextColor;
  text-decoration: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1440px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}